.messageWrapper {
  padding: 10px;
  height: inherit;
  overflow-y: scroll;
  padding-bottom: 5px;
}
.messageWrapper ul {
  display: flex;
  flex-direction: column;
}
.messageWrapper ul li {
  list-style: none;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
}
.messageWrapper ul li div {
  height: fit-content;
  word-wrap: break-word;
  padding: 12px;
  max-width: 60%;
  width: fit-content;
}
.sentByMe {
  color: #707c97;
  border-radius: 10px 10px 0px 10px;
  background: #ffffff;
  border: 1px solid rgba(112, 124, 151, 0.25);
  box-sizing: border-box;
  box-shadow: 10px 10px 25px rgba(112, 124, 151, 0.05), 15px 15px 35px rgba(112, 124, 151, 0.05);
}
.sentByFriend {
  color: #ffffff;
  border-radius: 0px 10px 10px 10px;
  background: var(--blueButtonColor);
  box-shadow: 10px 10px 25px rgba(42, 139, 242, 0.1), 15px 15px 35px rgba(42, 139, 242, 0.05),
  10px 10px 50px rgba(42, 139, 242, 0.1);
}
.moveRight {
  align-items: flex-end;
}
.moveLeft {
  align-items: flex-start;
}
.messageWrapper ul li .timeago {
  font-size: 12px;
  line-height: 26px;
  color: rgba(112, 124, 151, 0.7);
}